import React, { useEffect } from 'react'
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useState } from 'react';

import ToggleButton from '../components/ToggleButton';
import CheckAuthButton from '../components/CheckAuthButton';

const EditProfile = () => {

  const navigate = useNavigate();
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [first_name, setfirstName] = useState('');
  const [last_name, setlastName] = useState('');
  const [message, setMessage] = useState('');
  
  const redirect = () => {
    console.log("Redirect called");  // Debugging line
    navigate('/home');
  }

  useEffect(() => {
    // Function to get user details
    const getUserDetails = async () => {

      try {
        // Assuming you need to send the access token to validate the request
        const token = localStorage.getItem('access_token');
  
        // Make GET request to fetch user details
        const response = await axios.get('http://127.0.0.1:8000/get-account-data/', {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });
  
        // If request is successful, populate state
        if (response.status === 200) {
          setUsername(response.data.username); // Assuming the returned object has a 'username' field
          setEmail(response.data.email); // Assuming the returned object has an 'email' field
          setfirstName(response.data.first_name); // Assuming the returned object has a 'first_name' field
          setlastName(response.data.last_name); // Assuming the returned object has a 'last_name' field
        }
  
      } catch (error) {
        console.error("There was an error fetching user details:", error);
      }
    };
  
    // Call the function
    getUserDetails();
  }, []);  // Empty dependency array means this useEffect runs once when the component mounts

  const update_data = async () => {

    try {
      // Assuming you need to send the access token to validate the request
      const token = localStorage.getItem('access_token');

      console.log("This is the token: ", token);
      console.log("This is the username: ", username);
      console.log("This is the email: ", email);
      console.log("This is the first name: ", first_name);
      console.log("This is the last name: ", last_name);

      // Make POST request to update user details
      const response = await axios.post('http://127.0.0.1:8000/edit_profile/', 
        {
          first_name,
          last_name,
          username,
          email,
        }, 
        {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        }
      );

      // If request is successful, populate state
      if (response.status === 200) {

        setMessage('User details updated successfully!');
        
      }

    } catch (error) {

      console.error("There was an error updating user details:", error);
      setMessage('Something went wrong :/');

    };

  } // Empty dependency array means this useEffect runs once when the component mounts
  

  return (
    <div>
      <div id="container">

        <label>{message}</label>
        
        <h1>Edit Profile</h1>

        <div id='input-div'>

          <label id='input-label'>First Name</label>
          <input
                  id='input-field'
                  type = "text"
                  placeholder={first_name}
                  onChange={e => setfirstName(e.target.value)}

                />

        </div>

        <div id='input-div'>

          <label id='input-label'>Last Name</label>
          <input
                  
                  id='input-field'
                  type = "text"
                  placeholder={last_name}
                  onChange={e => setlastName(e.target.value)}

                />

        </div>

        <div id='input-div'>

          <label id='input-label'>Username</label>
          <input

                  id='input-field'
                  type = "text"
                  placeholder={username}
                  onChange={e => setUsername(e.target.value)}

                />

        </div>

        <div id='input-div'>

          <label id='input-label'>E-Mail</label>
          <input

                  id='input-field'
                  type = "text"
                  placeholder={email}
                  onChange={e => setEmail(e.target.value)}

                />

        </div>

        <button onClick={redirect}>Back</button>
        
        <button onClick={update_data}>Update</button>

        <ToggleButton/>

        <CheckAuthButton/>

      </div>
    </div>
  )
}

export default EditProfile
